import React, { useState } from "react";
import "./Navbar.css";
import { FiMenu, FiX } from "react-icons/fi";

const Navbar = ({ navbarLinks }) => {
  const [menuClicked, setMenuClicked] = useState(false);
  const toggleMenuClicked = () => {
    setMenuClicked(!menuClicked);
  };
  return (
    <nav className="navbar">
      <span className="navbar_logo">
        <a href="#home">Logo Here</a>
      </span>
      {menuClicked ? (
        <FiX size={25} className="navbar_menu" onClick={toggleMenuClicked} />
      ) : (
        <FiMenu size={25} className="navbar_menu" onClick={toggleMenuClicked} />
      )}
      <ul
        className={
          menuClicked ? "navbar_list navbar_list-active" : "navbar_list"
        }
      >
        {navbarLinks.map((ietm) => {
          return (
            <li className="navar_list_item" key={ietm.title}>
              <a className="navbar_link" href={ietm.url}>
                {ietm.title}
              </a>
            </li>
          );
        })}
      </ul>
    </nav>
  );
};

export default Navbar;
